/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
	margin: 0;
	padding: 0;
	background: #d9d9d9;
	font-family: "Helvetica Neue","Apple SD Gothic Neo",sans-serif;
	-webkit-font-smoothing: antialiased;
    font-weight: 500;
    font-size: 15px;
    line-height: 1.2;
}

code {
	font-family: "Courier New", monospace;
}

a {
	text-decoration: none;
}

.app {
    padding-top: 65px;
}
.blind {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin-top: -1px;
    margin-left: -1px;
    padding: 0;
    outline: 0;
    font-size: 1px;
    overflow: hidden;
}
.article-area {
    padding: 15px;
}
.article-text {
    margin: 15px 0;
    font-size: 18px;
    text-align: center;
}
.article-text a {
    color: rgb(242,100,35);
}
.search-info {
    margin: 15px 0;
    font-size: 18px;
    text-align: center;
}
.search-area {
    width: 300px;
    height: 60px;
    margin: 20px auto;
    background: rgb(242,100,35);
    border-radius: 30px;
    text-align: center;
}
.search-input,
.search-button {
    display: inline-block;
    vertical-align: top;
    margin-top: 15px;
    box-sizing: border-box;
    font-size: 15px;
}
.search-input {
    -webkit-appearance: none;
    width: 160px;
    height: 30px;
    border: 0;
}
.search-button {
    width: 80px;
    height: 30px;
    line-height: 29px;
    margin-left: 6px;
    border: 1px solid #fff;
    color: #fff;
    cursor: pointer;
}
.header {
	position: fixed;
	z-index: 20;
	top: 0;
	left: 0;
	right: 0;
	height: 50px;
	background: #fff;
	box-shadow: 0 2px 2px 0 rgba(0,0,0,.15);
}
.site-title {
	position: absolute;
	top: 0;
	left: 60px;
	line-height: 50px;
}
.site-title-link {
	font-size: 23px;
	font-weight: 600;
	text-transform: uppercase;
	color: #000;
}
.navbar-button {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	left: 15px;
	width: 30px;
	height: 25px;
	border: solid #000;
	border-width: 2px 0;
	box-sizing: border-box;
	cursor: pointer;
}
.navbar-button:before {
	content: '';
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	left: 0;
	right: 0;
	height: 2px;
	background: #000;
}
.navbar-button[aria-pressed="true"] {
	border-color: rgb(242,100,35);
}
.navbar-button[aria-pressed="true"]:before {
	background: rgb(242,100,35);
}
.navbar-area {
	overflow: scroll;
	position: fixed;
	z-index: 10;
	top: 50px;
	left: -250px;
	bottom: 0;
	width: 250px;
	background: #fff;
	box-shadow: 0 2px 2px 0 rgba(0,0,0,.15);
}
.navbar-area[aria-expanded="false"] {
	left: -250px;
	-webkit-animation-duration:0.3s;
	        animation-duration:0.3s;
	-webkit-animation-name: slideOut;
	        animation-name: slideOut;
}
.navbar-area[aria-expanded="true"] {
	left: 0;
	-webkit-animation-duration:0.3s;
	        animation-duration:0.3s;
	-webkit-animation-name: slideIn;
	        animation-name: slideIn;
}
@-webkit-keyframes slideIn {
	0% { left: -250px; }
	100% { left: 0; }
}
@keyframes slideIn {
	0% { left: -250px; }
	100% { left: 0; }
}
@-webkit-keyframes slideOut {
	0% { left: 0; }
	100% { left: -250px; }
}
@keyframes slideOut {
	0% { left: 0; }
	100% { left: -250px; }
}
.navbar-list {
	position: relative;
}
.navbar-item {
	border-bottom: 1px solid #e1e1e1;
}
.navbar-link {
	display: block;
	padding: 18px 15px;
	font-size: 15px;
	color: #000;
}
.navbar-link:hover {
	background: #f1f1f1;
}
.video-iframe {
    display: block;
    border: none;
    width: 100%;
    height: 250px;
}

@media (min-width: 768px) {
	.video-iframe {
		height: 500px;
	}
}
@media (min-width: 1700px) {
	.video-iframe {
		height: 900px;
	}
}
.content-area {
	max-width: 1500px;
	margin: 0 auto;
	padding: 15px;
}
.content-list {
	margin: 0 -5px;
}
.content-item { 
	position: relative;
	display: inline-block;
	width: 100%;
	margin-bottom: 15px;
	padding: 0 5px;
	box-sizing: border-box;
	vertical-align: top;
}

@media (min-width: 512px) {
	.content-item {
		width: 50%;
	}
}
@media (min-width: 640px) {
	.content-item {
		width: 33.3333%;
	}
}
@media (min-width: 768px) {
	.content-item {
		width: 25%;
	}
}
@media (min-width: 896px) {
	.content-item {
		width: 20%;
	}
}
@media (min-width: 1024px) {
	.content-area {
		padding: 30px;
	}
	.content-item {
		width: 16.6666%;
	}
}
.thumbnail-area {
	position: relative;
	display: block;
}
.thumbnail-area:after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	border: 1px solid rgba(0,0,0,0.05);
}
.thumbnail {
	width: 100%;
	vertical-align: top;
}
.video-title {
	display: block;
	margin-top: 6px;
	word-break: break-all;
	text-align: center;
}
.channel-title {
	display: block;
	margin-top: 6px;
	text-align: center;
	color: #888;
}

